.j-container .j-infos {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

@media screen and (max-width: 768px) {
  .j-container,
  .j-infos {
    display: flex;
    flex-direction: column;
  }
  .j-container {
    & > div {
      width: 100%;
    }
    .j-infos {
      // margin-top: 0;
    }
  }
}

@media screen and (min-width: 769px) and (max-width: 1340px) {
  .j-wrapper .ant-table-wrapper {
    overflow-x: auto;
  }
}
