// mobile
@media screen and (max-width: 768px) {
  @function vw($size) {
    @return #{math.div($size, 3.9)}vw;
  }

  .account-v2-container {
    width: 100%;
  }

  .account-v2-container .title .content {
    font-size: 16px;
    line-height: vw(22);
    border-bottom: 1px dashed #0f134f;
  }

  .account-v2-container {
    .account-main-info {
      box-shadow: 0px vw(10) vw(20) rgba(0, 0, 0, 0.05);
      border-radius: 2px;
      padding: vw(20);
      .rist-value {
        overflow: hidden;
        margin-top: vw(30);
      }
      .account-gauge {
        margin: 0 0 0 vw(10);
      }
      .risk-warning-tip {
        width: 100%;
        padding: 0 10px 0 28px;
        .text,
        .text > span {
          font-size: 12px;
          line-height: 24px;
        }
        .close {
          width: 8px;
          height: 8px;
          background-size: contain;
        }
      }
      // .countDatas {
      //   .btn {
      //     width: 26px;
      //     height: 26px;
      //   }
      // }
    }
    .net-value {
      margin-left: vw(54);
      p {
        margin-bottom: vw(4);
      }
      .fz12 {
        font-size: 12px;
        line-height: vw(20);
      }
      .fz18 {
        font-size: 18px;
        line-height: vw(25);
      }
      .mt17 {
        margin-top: vw(17);
      }
    }
    .data-info-key {
      font-size: 12px;
      line-height: vw(20);
      margin-bottom: vw(4);
    }
    // 待分配收益
    .more {
      padding: vw(20);
      background-size: 100%;
      box-shadow: 0px 1px 2px rgb(0 0 0 / 10%);
      border-radius: 2px;
      margin-top: vw(10);
      position: relative;

      .waiting-reward-icon {
        width: vw(40);
        height: vw(40);
        right: vw(20);
        top: vw(21);
      }
      .value {
        line-height: vw(25);
      }
    }
  }

  .ant-modal.account-invest-modal {
    width: 100%;
    min-width: 0;

    .ant-modal-content .ant-modal-close-x {
      background: none;
    }
  }
}

.modal-info-table-v2 {
  .ant-table-body {
    &::-webkit-scrollbar {
      width: 1px;
    }
    &::-webkit-scrollbar-track {
      background: transparent;
      border-radius: 2px;
    }

    &::-webkit-scrollbar-thumb {
      background: rgba(255, 255, 255, 0.2);
      border-radius: 13px;
    }
  }
}

.white {
  .risk-warning-tip-line {
    border-bottom: 1px solid rgba(34, 35, 43, 0.1);
  }
