@use 'sass:math';
.j-container {
  .j-market-list {
    margin-top: 20px;
  }
}

.j-market-list {
  padding-bottom: 5px;
  background: #31333a;
  border-radius: 10px;
  overflow: hidden;
  .ant-table {
    background: transparent;
  }
  .j-token {
    margin-left: 16px;
    font-size: 16px;
    .token-names {
      font-weight: 600;
      font-size: 16px;
      line-height: 19px;
      color: #ffffff;
    }
    .description {
      font-weight: 400;
      font-size: 12px;
      line-height: 16px;
      color: rgba(255, 255, 255, 0.6);
    }
  }
  .j-supply {
    margin-right: 12px;
  }
  .ant-table-tbody {
    tr {
      td {
        &:nth-child(2),
        &:nth-child(3),
        &:nth-child(4) {
          padding-right: 22px;
        }
        span {
          font-size: 14px;
        }
      }
    }
  }
  .ant-table-thead th,
  .ant-table-tbody td {
    &:nth-child(6) {
      text-align: center;
    }
  }
  .ant-table-thead th {
    font-size: 14px !important;
    // padding-bottom: 6px;
    // border-bottom: 1px solid rgba(255, 255, 255, 0.06) !important;
    span {
      font-size: 14px !important;
    }
  }
  .ant-table-thead > tr > th .ant-table-column-sorter {
    transform: translateY(-4px);
  }
  .j-tooltip-icon {
    transform: translateY(2px);
    &.j-info-icon1 {
      transform: translateY(0px);
    }
  }
  .fire-icon {
    position: absolute;
    right: -17px;
    top: -1px;
  }
  .ant-table-container table > thead > tr:first-child th:last-child {
    border-radius: 0;
  }
  .j-deposit-apy {
    &:hover {
      > span:first-child {
        color: #9195fb;
      }
    }
  }
}

.market-top {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  > div:first-child {
    font-weight: 600;
    font-size: 20px;
    color: #ffffff;
    text-shadow: 0px 2px 10px rgba(0, 0, 0, 0.5);
  }
}
.market-right {
  display: flex;
  align-items: center;
}

.show-balance {
  > span:last-child {
    padding-left: 6px;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    text-align: right;
    color: rgba(255, 255, 255, 0.6);
    white-space: nowrap;
  }
  .ant-checkbox-checked .ant-checkbox-inner::after {
    border: 2px solid rgba(255, 255, 255, 1);
    border-top: 0;
    border-left: 0;
  }
  .ant-checkbox-checked::after {
    border-color: #4d54ff;
  }
  .ant-checkbox-inner,
  .ant-checkbox-checked .ant-checkbox-inner {
    background: #22232b;
    border-radius: 1px;
    border-color: #313339 !important;
  }

  .ant-checkbox-checked .ant-checkbox-inner {
    background: #4d54ff;
    border-radius: 1px;
    border-color: #4d54ff !important;
  }

  .ant-checkbox-wrapper:hover .ant-checkbox-inner,
  .ant-checkbox:hover .ant-checkbox-inner,
  .ant-checkbox-input:focus + .ant-checkbox-inner {
    border-color: #313339;
  }
  .ant-checkbox {
    transform: translateY(1px);
  }
}

.search-token {
  width: 240px;
  height: 26px;
  line-height: 26px;
  background: #22232b;
  border-radius: 2px;
  border: none;
  margin-left: 30px;

  &.ant-input-affix-wrapper,
  &.ant-input-affix-wrapper:not(.ant-input-affix-wrapper-disabled):hover,
  .ant-input-affix-wrapper:focus,
  .ant-input-affix-wrapper-focused {
    border-color: #22232b;
    box-shadow: none;
  }
  .ant-input {
    background: #22232b;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    &::-webkit-input-placeholder {
      color: #a7a7aa;
      font-weight: 500;
    }
    color: rgba(255, 255, 255, 1);
  }
  .ant-input:focus,
  .ant-input-focused,
  &:hover {
    border: none;
  }
  .ant-input-prefix {
    margin-right: 12px;
    color: #ffffff;
    span {
      color: #ffffff;
    }
  }
  .ant-input-clear-icon {
    width: 10px;
    height: 10px;
    background: url(../../images/v2/clear-icon.svg) 0 0 no-repeat;
    background-size: 100%;
    > svg {
      display: none;
    }
  }
}
.deposit .ant-input-prefix {
  color: #ffffff;
}
.fire-icon {
  margin-left: 6px;
  margin-top: 4px;
  &.hide-icon {
    background: none;
  }
}
.j-wrapper.white {
  $gray_bg: rgba(75, 82, 223, 0.06);

  .j-market-list {
    background-color: #ffffff;
    .token-names {
      color: #22232b;
    }
    .description {
      color: rgba(34, 35, 43, 0.6);
    }
    .ant-input,
    .ant-input-affix-wrapper {
      border: none;
    }
    .j-deposit-apy {
      &:hover {
        > span:first-child {
          color: #4c54ff;
        }
      }
    }
  }
  .market-top > div:first-child {
    color: #22232b;
    text-shadow: 0px 2px 10px rgba(255, 255, 255, 0.5);
  }

  .show-balance {
    > span:last-child {
      color: rgba(0, 0, 0, 0.6);
    }
    // .ant-checkbox-checked .ant-checkbox-inner::after {
    //   border: 2px solid rgba(75, 82, 223, 0.3);
    //   border-top: 0;
    //   border-left: 0;
    // }
    .ant-checkbox-checked::after {
      border-color: $gray_bg;
    }
    .ant-checkbox-inner {
      background: $gray_bg;
      border-color: transparent !important;
    }
    // .ant-checkbox-checked .ant-checkbox-inner {
    //   background: $gray_bg;
    //   border-color: $gray_bg !important;
    // }

    .ant-checkbox-checked .ant-checkbox-inner {
      background: #4d54ff;
      border-radius: 1px;
      border-color: #4d54ff !important;
    }
    .ant-checkbox-wrapper:hover .ant-checkbox-inner,
    .ant-checkbox:hover .ant-checkbox-inner,
    .ant-checkbox-input:focus + .ant-checkbox-inner {
      border-color: $gray_bg;
    }
  }

  .search-token {
    background: $gray_bg;

    &.ant-input-affix-wrapper:not(.ant-input-affix-wrapper-disabled):hover,
    .ant-input-affix-wrapper:focus,
    .ant-input-affix-wrapper-focused {
      border-color: $gray_bg;
    }
    .ant-input {
      background: transparent;
      &::-webkit-input-placeholder {
        color: #76777f;
        font-weight: 500;
      }
      color: rgba(34, 35, 43, 1);
    }

    .ant-input:focus,
    .ant-input-focused {
      border: none;
    }
    .ant-input-prefix {
      margin-right: 12px;
    }
    .ant-input-clear-icon {
      background-image: url(../../images/v2/white-theme/clear-icon.svg);
    }
  }
  .deposit .ant-input-prefix {
    color: rgba(0, 0, 0, 0.85);
  }
}

@media screen and (max-width: 768px) {
  @function vw($size) {
    @return #{math.div($size, 3.9)}vw;
  }
  .j-container .j-market-list {
    margin-top: vw(20);
    .j-supply {
      margin-bottom: 4px;
    }
  }
  .market-top {
    flex-direction: column;
    padding: vw(20);
    & > div:first-child {
      font-size: 16px;
      text-shadow: 0px vw(2) vw(10) rgb(0 0 0 / 50%);
    }
  }
}
